.footer {
    background-color: #333;
    color: white;
    padding: 50px 10px 30px 40px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start; /* Align items at the start of the footer */
    flex-wrap: wrap;
}

.footer-section {
    flex: 1 1 20%; /* Flex grow, flex shrink, and basis adjusted */
    padding: 10px;
}

.footer-section h4, .footer-section p {
    margin-bottom: 10px; /* Reduced space below headers and paragraphs */
}

.watsus-section {
    display: flex;
    flex-direction: column; /* Stack elements vertically */
    align-items: flex-start; /* Align items to the start */
}

.social-links {
    display: flex;
    align-items: center;
    justify-content: flex-start; /* Align icons horizontally and to the start */
    margin-bottom: 20px; /* Space below the icons */
    color: white; /* Sets the icon color to white */
}

.social-links a {
    font-size: 24px; /* Icon size */
    margin-right: 15px; /* Right margin for spacing between icons */
    color: white; /* Ensure the text color is white */
}


.footer-section form {
    display: flex;
    gap: 10px;
}

.footer-section input[type="email"] {
    flex: 1; /* Allows the input to grow */
    padding: 10px;
    border: none;
    background: #444; /* Darker background for the input field */
    color: white;
}

.footer-section button {
    background-color: teal;
    border: round;
    color: white;
    padding: 10px 20px;
    cursor: pointer;
}

.links {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.links > div {
    flex: 1;
}

.links ul {
    list-style-type: none;
    padding: 0;
}

.links li a {
    color: white;
    text-decoration: none;
    display: block;
    margin-bottom: 10px;
}

@media (max-width: 768px) {
    .footer {
      flex-direction: column;
    }

    .footer-section, .links {
      width: 100%;
    }

    .links {
      flex-direction: column;
      align-items: center;
    }

    .links > div {
      margin-bottom: 20px;
    }
}
