.card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #168e97;
    border-radius: 24px;
    padding: 90px;
    color: white;
    margin: 55px;
    position: relative; /* Added for potential absolute positioning inside the card */
}

.card-content {
    flex: 1;
    padding-right: 40px; /* Adds some spacing between text and image */
}

.card-title {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 30px;
}

.card-subtitle {
    font-size: 24px;
    opacity: 0.8;
    margin-bottom: 20px;
}

.search-area {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.search-input {
    padding: 10px;
    margin-right: 10px;
    border-radius: 12px;
    border: none;
    outline: none;
    width: 300px;
}

.search-button {
    background-color: white;
    color: black;
    border: none;
    border-radius: 12px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 18px;
}

.card-image {
    width: 180px; /* Adjust size as needed */
    height: 180px; /* Adjust size as needed */
    border-radius: 50%; /* This makes it a circle */
    overflow: hidden; /* Ensures no overflow outside the circle */
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-image img {
    width: 100%; /* Ensure the image covers the entire container */
    height: auto; /* Maintain aspect ratio */
    min-height: 100%; /* Ensures the image covers the height fully */
}


.qr-code {
    width: 250px; /* Adjust the width to your preference */
    height: 250px; /* Adjust the height to match the width for a square aspect ratio */
    /* Additional styling if needed */
  }
  