.category-selector {
    padding: 50px;
    background-color: white;
    border-radius: 20px;
    color: black; /* Changed to black for better visibility on white background */
    display: flex;
    flex-direction: column;
    align-items: start;
    margin: 55px;
    padding-top: 10px;
    padding-bottom: 30px;
}

.category-content h1, .category-content p {
    margin: 100px; /* Added units to margin */
}

.category-dropdown {
    margin: 20px 0;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.category-cards {
    padding-top: 30px;
    display: flex;
    flex-wrap: wrap;
    gap: 65px;
    justify-content: start;
}

.category-card {
    background-color: #333;
    color: white;
    width: 150px;
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    cursor: pointer;
    overflow: hidden;
}

.category-card.active {
    border: 2px solid whitesmoke;
    background-color: teal; /* Highlight active card with teal background */
}

.category-image {
    width: 100%;
    height: 80px; /* Adjusted height for better proportion */
    object-fit: cover;
    margin-bottom: 20px; /* Increased gap between the image and the title */
}

.category-title, .category-name { /* Unified class for titles to avoid duplication */
    font-size: 14px;
    font-weight: bold; /* Bold text for better emphasis */
    color: white; /* Ensured text is white for visibility against dark background */
}
