.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: white;
    padding: 10px 40px; 
    width: 95%;
}

.logo img {
    height: 50px;  
}

.nav-links {
    list-style: none;
    display: flex;
    margin-left: 0; 
    padding-left: 0; 
    gap: 30px;
}

.nav-links li a {
    color: white;
    text-decoration: none;
    font-size: 16px;
    font-family: "Source Sans Pro";
    font-weight: 700;
    line-height: 22px;
    text-align: center;
}

.search-and-icons {
    display: flex;
    align-items: center;
    gap: 10px;
}

.search-and-icons .icon {
    cursor: pointer;
}

.search-and-icons input {
    padding: 8px;
    border: 2px solid #ccc;
    border-radius: 4px;
    background: transparent;
    color: white;
    outline: none;
}
